.store3Content .section1 {
  max-width : 1350px;
  margin : 0 auto;
  padding-bottom : 136px;
  display : flex;
  align-items : center;
}
.store3Content .section1 .img {
  width : 45%;
}
.store3Content .section1 .desc {
  width : 55%;
}
.store3Content .section1 .desc .title {
  position : relative;
  margin-bottom : 20px;
  padding-left : 40px;
}
.store3Content .section1 .desc .title::before {
  content : '';
  position : absolute;
  background : url('/haemulhansang/img/store/sstit.png') no-repeat;
  width : 40px;
  height : 60px;
  left : 0;
  top : -20px;
}
.store3Content .section1 .desc .title::after {
  content : 'Interior Concept';
  position : absolute;
  color : #1c4834;
  font-weight : bold;
  top : -22px;
  left : 40px;
}
.store3Content .section1 .desc .info {
  margin-bottom : 15px;
}
.store3Content .section1 .desc .info .label {
  margin-bottom : 10px;
  color : #056437;
  font-size : 1.125em;
  font-weight : bold;
}
.store3Content .section1 .desc .info .text {
  color : #606060;
  font-size : 1em;
  letter-spacing : -1.5px;
}
.store3Content .section2 {
  width : 100%;
  background : url('/haemulhansang/img/store/interior/interior_bg.jpg');
  background-size : cover;
  padding: 116px 0 114px;
  margin-bottom : 136px;
}
.store3Content .section2 .section2ContWrap {
  max-width : 1350px;
  margin : 0 auto;
  text-align : center;
}
.store3Content .section2 .section2ContWrap .title {
  margin-bottom : 25px;
}
.store3Content .section3 {
  max-width : 1350px;
  margin : 0 auto;
}
.store3Content .section3 .btnWrap {
  margin-bottom : 60px;
  display : flex;
  justify-content : center;
}
.store3Content .section3 .btnWrap .btn {
  width : 204px;
  height : 55px;
  background : #6cb968;
  display : flex;
  align-items : center;
  justify-content : center;
  margin-right : 25px;
}
.store3Content .section3 .btnWrap .btn:nth-child(2) {
  margin-right : 0;
}
.selectedBtn {
  background : url('/haemulhansang/img/store/interior/interior_tab_on.png') no-repeat !important;
  background-size : 100% 100% !important;
}
.imgSlide {
  width : 100%;
  padding-bottom: 52%;
  overflow : hidden;
  position : relative;
}
.slideImg {
  position : absolute;
}
.imgSelect {
  width : 100%;
  height : auto;
  position : relative;
  display : flex;
  flex-wrap : wrap;
  margin-top : 10px;
}
.imgSelect img {
  display : block;
  width : calc((94%) / 7 - 0.01px);
  margin-right : 1%;
  min-height : 25px;
}
.imgSelect img:nth-last-child(1) {
  margin-right : 0;
}

@media screen and (min-width : 768px) and (max-width : 1180px) {
  .store3Content .section1 {
    flex-direction : column;
    align-items : center;
  }
  .store3Content .section1 .img {
    width : 100%;
    text-align : center;
  }
  .store3Content .section1 .desc {
    width : 540px;
    margin-top : 6vw;
  }
  .store3Content .section2 .section2ContWrap .desc {
    width : 80%;
    letter-spacing : -1px;
    margin : 0 auto;
  }
  .imgSelect img {
    height : 50px;
  }
}
@media screen and (min-width : 0px) and (max-width : 767px) {
  .store3Content .section1 {
    flex-direction : column;
    align-items : center;
    padding : 0 4vw;
  }
  .store3Content .section1 .img {
    width : 100%;
    text-align : center;
  }
  .store3Content .section1 .img img {
    display : block;
    width : 100%;
  }
  .store3Content .section1 .desc .title {
    text-align : center;
  }
  .store3Content .section1 .desc .title::before {
    left : 50%;
    margin-left : -100px;
  }
  .store3Content .section1 .desc .title::after {
    left : 50%;
    margin-left : -60px;
  }
  .store3Content .section1 .desc {
    width : 100%;
    margin-top : 10vw;
  }
  .store3Content .section1 .desc .info .label {
    font-size : 5vw;
    text-align : center;
  }
  .store3Content .section1 .desc .info .text {
    font-size : 4vw;
    text-align : center;
  }
  .store3Content .section2 .section2ContWrap .title {
    width : 80%;
    margin : 0 auto;
    margin-bottom : 6vw;
  }
  .store3Content .section2 .section2ContWrap .title img {
    display : block;
    width : 100%;
  }
  .store3Content .section2 .section2ContWrap .desc {
    width : 80%;
    letter-spacing : -1px;
    margin : 0 auto;
  }
  .store3Content .section3 .btnWrap {
    width : 80%;
    margin : 0 auto;
    margin-bottom : 6vw;
  }
  .store3Content .section3 .btnWrap .btn {
    width : 100%;
  }
  .store3Content .section3 .btnWrap .btn img {
    display : block;
    width : 70%;
  }
  .imgSelect img {
    height : 25px;
  }
}