.compContent {
  max-width : 1350px;
  padding : 0 20px;
  margin : 0 auto;
}
.compContent .img {
  width : 50%;
}
.compContent .img img {
  display : block;
  width : 100%;
}
.compContent .section .text {
  width : 45%;
}
.compContent .section .title {
  font-size : 2.5em;
  font-weight : bold;
  margin-bottom : 3vh;
}
.compContent .section .desc {
  font-size : 1em;
  font-weight : 400;
  color : #353535;
  word-break : keep-all;
  letter-spacing : -0.025em;
}
.compContent .sectionType1 .img {
  margin-right : 5%;
}
.compContent .sectionType1 .text {
  text-align : left;
}
.compContent .sectionType2 .img {
  margin-left : 5%;
}
.compContent .sectionType2 .text {
  text-align : right;
}

.compContent .logo {
  position : relative;
  text-align : center;
  margin-bottom : 60px;
}
.compContent .logo::before {
  content : '';
  position : absolute;
  width : 34px;
  height : 47px;
  background : url('/haemulhansang/img/about/comp/sstit.png') no-repeat;
  left : 50%;
  margin-left : -124px;
  top : -15px;
}
.compContent .logo::after {
  content : 'Brand Competitive';
  position : absolute;
  color : #1c4834;
  font-size : 1em;
  font-weight : bold;
  left : 50%;
  margin-left : -80px;
  top : -20px;
}
.compContent .section {
  display : flex;
  flex-wrap : wrap;
  align-items : center;
  margin : 80px 0;
}

@media screen and (min-width : 768px) and (max-width : 1180px) {
  .introContent .section1, .introContent .section3, .introContent .section5 {
    padding : 0 4vw;
  }
}
@media screen and (min-width : 0px) and (max-width : 767px) {
  .compContent .section {
    flex-direction : column;
  }
  .compContent .section .img, .compContent .section .text {
    width : 100%;
    margin : 0;
  }
  .compContent .section .text {
    margin-top : 8vw;
  }
  .compContent .section .title {
    font-size : 7vw;
    text-align : center;
  }
  .compContent .section .desc {
    font-size : 4vw;
    text-align : center;
  }
  .compContent .sectionType2  {
    flex-direction : column-reverse;
  }
}